<template>
   <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        
        transition="scale-transition"
        min-width="290px"
      >
    <template v-slot:activator="{ on, attrs }"> 
      <v-text-field rounded outlined dense  :value="dateRangeText" label="Date range" readonly v-bind="attrs" v-on="on" clearable @click:clear="clear"></v-text-field>
    </template>
   <v-date-picker v-model="dates" range @input="input"></v-date-picker>
   </v-menu>
</template>
<script>
 
// function pad(number, length) {
   
//     var str = '' + number;
//     while (str.length < length) {
//         str = '0' + str;
//     }

//     return str;

// }
export default {
  name: 'date-picker',
  props: {
    value: Array,
  },
  data() {
    //  let today = new Date();
    // today.setHours(0, 0, 0, 0);
    // const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
   
    return {
      menu: false,
      dates:[]
    }
  },
  methods: {
    input(e ) {
      if (e.length > 1) {
         this.menu = false
         this.$emit('input', e)
      }
    },
    clear () {
      this.dates = []
      this.$emit('input', [])
    }

  },
  computed: {
    dateRangeText () {
      return this.dates.join(' ~ ')
    },
     
  },
  created () {
    if (this.value)
      this.dates = this.value
    // else
    //    this.$emit('input', this.dates)
  }
 
}
</script>