<template>
<v-container>
 
  <v-card>
    <v-card-text class="px-6">
      <v-form ref="form"> 
        <v-row>
          <v-col cols="12" sm="5"  >
              <v-select  required :rules="[v => !!v || 'this field is required']" rounded outlined dense  label="Report" v-model="type" :items="types" item-text="text" item-value="value"></v-select>
          </v-col>
          <v-col cols="12" sm="7"  >
              <app-datepicker v-model="dates"></app-datepicker>
          </v-col>
          <v-col cols="12" sm="9" >
              <app-autocomplete  rounded outlined dense v-bind="autocomplete.bind" v-model="autocomplete.value"></app-autocomplete>
          </v-col>
          <v-col cols="12" sm="3" >
              <v-btn large rounded color="green white--text" width="100%" @click="submit">Submit</v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
  </v-card>
   <v-data-table 
        class="my-5 elevation-1"
        :headers="headers"
        :single-expand="singleExpand"
        :expanded.sync="expanded"
        show-expand
        item-key="_id._id"
        :items="items">
    <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template>   
    <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length" class="pa-4">
        <v-data-table :headers="dataHeaders" :items="dataItems" @click:row="innerListClick($event, item)">
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template>   
          <template v-slot:item.created_on="{ item }"  >
            {{ new Intl.DateTimeFormat().format(new Date(item.created_on)) }}
          </template>
      
        </v-data-table>
      </td>
    </template> 
      
  </v-data-table>
  <v-dialog v-model="dialog.value" scrollable max-width="960px">
      <v-card>
        <v-card-title v-html="dialog.title"></v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <v-data-table :headers="dialog.headers" :items="dialog.items">
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template>   
          <template v-slot:item.created_on="{ item }"  >
            {{ new Intl.DateTimeFormat().format(new Date(item.created_on)) }}
          </template>
      
        </v-data-table>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="blue darken-1" text @click="dialog.value = false">Close</v-btn>
       
        </v-card-actions>
      </v-card>
    </v-dialog>

</v-container>

 
</template>
<script>
import AppAutoComplete from '../components/Autocomplete'
import AppDatePicker from '../components/DateRange'
import { mapActions } from 'vuex'
 

const leadHeaders = [
          {
            text: 'TP Account',
            align: 'start',
            value: '_id.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: '_id.name',
        },
        { text: 'Affiliate', value: '_id.affiliate.name' },

        { text: 'Deposits', value: 'count', width: '10%' },
        { text: 'Total', value: 'amount' }]

const affiliateHeaders = [
    
        {
          text: 'Affiliate',
          align: 'start',
          value: '_id.name',
        },
        { text: 'Deposits', value: 'count', width: '10%' },
        { text: 'Total', value: 'amount' }]
const agentsHeaders = [
    
        {
          text: 'Agent',
          align: 'start',
          value: '_id.displayName',
        },
        { text: 'Deposits', value: 'count', width: '10%' },
        { text: 'Total', value: 'amount' }]




// inner table
const dataHeadersLeads = [
        { text: 'Department', value: 'department.name' },
        { text: 'Date', value: 'created_on' },
        { text: 'Amount', value: 'amount' },
        { text: 'Payment Method', value: 'payment_method.name' }
      ]

const dataHeadersAffiliate = [
         {
            text: 'TP Account',
            align: 'start',
            value: '_id.tpc',
        },
        {
            text: 'Account name',
            align: 'start',
            value: '_id.name',
        },
        { text: 'Deposits', value: 'count', width: '10%' },
        { text: 'Amount', value: 'amount' },
      ]
const dataHeadersAgent = [
         {
            text: 'TP Account',
            align: 'start',
            value: '_id.tpc',
        },
        {
            text: 'Account name',
            align: 'start',
            value: '_id.name',
        },
         {
            text: 'Affilaite',
            align: 'start',
            value: '_id.affiliate.name',
        },
        { text: 'Deposits', value: 'count', width: '10%' },
        { text: 'Amount', value: 'amount' },
      ]
function pad(number, length) {
   
    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
export default {
  name: 'Report',
  components: {
    'app-autocomplete': AppAutoComplete,
    'app-datepicker': AppDatePicker
  },
  data() {
    let today = new Date();
    today.setHours(0, 0, 0, 0);
    const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
  
    return {
      dialog: {
        value: false,
        headers: dataHeadersLeads,
        items: [],
        title: ''
      },
      dataItems: [],
      dataHeaders: dataHeadersLeads,
      expanded: [],
      singleExpand: true,
      items: [],
      headers:leadHeaders,
      types: [
        { text: "By Lead", value: 'lead' },
        { text: "By Affiliate", value: 'affiliate' },
        { text: "By Agent", value: 'agent' }
      ],
      type: 'lead',
      autocomplete: {
        value: '',
        bind: {
          collection: 'customer',
          itemValue: '_id',
          itemText: 'name',
          hideAddNew: true,
          label: 'Select a lead',
          searchFields: ['name', 'tpc']
        }
      },
      dates 
    }
  },
  watch: {
    async expanded (item) {
      if(item.length) {
        
      switch (this.type) {
        case 'lead': {
          const res = await this.get({ collection: 'deposit', query: {
            created_on: this.dates.join('|'),
            customer: item[0]._id._id,
            sortBy:['created_on'],
            sortDesc: [true]
          }})
          this.dataItems = res.data.docs
          break;
        }
        case 'affiliate':{
          const query = { 
            created_on: this.dates.join('|'),
           'affiliate._id' :  item[0]._id._id
          }
          const result = await this.get({ collection: 'report_old/affiliate', query: { groupBy: 'customer._id', ...query, meta: {collection: 'customer'}, sortBy: ['customer.tpc', 'created_on'], sortDesc:[1, 1] } })
          
          console.log(result);
          this.dataItems = result.data
          break;
        }
        case 'agent':{
           const query = { 
            created_on: this.dates.join('|'),
           'user' : item[0]._id._id
          }
          const result = await this.get({ collection: 'report_old/agent', query: { groupBy: 'customer._id', ...query, meta: {collection: 'customer'}, sortBy: ['customer.affiliate.name', 'created_on'], sortDesc:[1, 1] } })
          
          console.log(result);
          this.dataItems = result.data
           
          break;}
      
      
      }

      }
    },
    type (type) {
      this.items = []
       this.autocomplete.value = ''
      switch (type) {
        case 'lead':
          this.autocomplete.bind.collection = 'customer'
          this.autocomplete.bind.itemText = 'name'
          this.autocomplete.bind.label = 'Select a lead'
          this.headers = leadHeaders
          this.dataHeaders = dataHeadersLeads
          break;
        case 'affiliate':
          this.autocomplete.bind.collection = 'affiliate'
          this.autocomplete.bind.itemText = 'name'
          this.autocomplete.bind.label = 'Select an affiliate'
          this.headers = affiliateHeaders
          this.dataHeaders = dataHeadersAffiliate
          break;
        case 'agent':
          this.autocomplete.bind.collection = 'user'
          this.autocomplete.bind.itemText = 'displayName'
          this.autocomplete.bind.label = 'Select an agent'
          this.headers = agentsHeaders
          this.dataHeaders = dataHeadersAgent

          break;
      
      
      }
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async innerListClick (item, parentItem) {
      if (this.type == 'affiliate') {
        const customerId = item._id._id
        const res = await this.get({ collection: 'deposit', query: {
            created_on: this.dates.join('|'),
            customer: customerId,
            limit: 1000,

            sortBy:['created_on'],
            sortDesc: [true]
        }})
        this.dialog.value = true
        this.dialog.items = res.data.docs
        this.dialog.title = `( <small>${ item._id.tpc }</small> ) ${item._id.name}`

      }
      if (this.type == 'agent') {
        const customerId = item._id._id
        const res = await this.get({ collection: 'deposit', query: {
              
            created_on: this.dates.join('|'),
            customer: customerId,
            user: parentItem._id._id,

            limit: 1000,
            sortBy:['created_on'],
            sortDesc: [true]
        }})
        this.dialog.value = true
        this.dialog.items = res.data.docs
        this.dialog.title = `( <small>${ item._id.tpc }</small> ) ${item._id.name}`

      }
    },
    async submit () {
      if (this.$refs.form.validate()) {
        const { collection } = this.autocomplete.bind
        const query = { 
          created_on: this.dates.join('|'),
        }
          let key;
         switch (collection) {
           case 'affiliate':
             key = 'customer.'+collection
             break;
           case 'customer':
             key = 'customer._id'
   
             break;
           default:
             key = collection
   
             break;
        }
         
       if (this.autocomplete.value) {

         query[key] = this.autocomplete.value

       }
       
       const result = await this.get({ collection: 'report_old', query: { groupBy: '$'+key, meta: {collection}, ...query, sortBy: ['_id.tpc'], sortDesc:[1] } })
       this.items = result.data
       console.log(result.data)

      } else 
        this.$store.commit('error', 'form not valid')

    }
  }
}
</script>